import "../App.css";

import { animate, motion } from "framer-motion";

import React, { useState, useEffect } from "react";

import { Outlet, Link, useNavigate } from "react-router-dom";

import logo from "../asstes/logo.png";
import background from "../asstes/background.jpg";

import phone from "../asstes/phone.png";
import loadingIcon from "../asstes/loadingIcon.gif";

import log from "../asstes/logo.svg";
import qr from "../asstes/qr.svg";

import { auth } from "../Components/database_config/firebaseConfig"; // Import auth
import { signInWithEmailAndPassword } from "firebase/auth"; // Import Firebase Auth methods
import { getDatabase, ref, get } from "firebase/database";

const Home = () => {
  const navigate = useNavigate();

  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const [loading, setLoading] = useState(false);
  const [showMsg, setShowMsg] = useState(false);

  const [showDashboard, setShowDashboard] = useState(false);

  const HandleLogin = async (e) => {
    e.preventDefault();

    setLoading(true);
    try {
      // Firebase login with email and password
      const userCredential = await signInWithEmailAndPassword(
        auth,
        email,
        password
      );

      

      verifyAdmin(userCredential.user.reloadUserInfo.localId).then((isAdmin)=> {
        if (isAdmin) {
          setShowDashboard(true);
  
          setTimeout(() => {
            navigate("/Dashboard", {
              state: {
                articleId: 5,
              },
            });
          }, 3000);
        }else{
          setShowMsg(true);
          setTimeout(() => setShowMsg(false), 5000);
        }
      })
      
      
    } catch (error) {
      console.error("Error logging in:", error.message);
      setShowMsg(true);

      setTimeout(() => setShowMsg(false), 5000);
    } finally {
      setLoading(false);
    }
  };

  const db = getDatabase();
  const verifyAdmin = async (uid) => {
    try {
      const adminRef = ref(db, "admin/userId");
      const snapshot = await get(adminRef);

      if (snapshot.exists() && snapshot.val() === uid) {
        console.log("Verified as Admin!");
        // Redirect to admin dashboard or allow access
        return true;
      } else {
        console.log("Not an Admin.");
        // Show an error message or redirect
        return false;
      }
    } catch (error) {
      console.error("Error verifying admin:", error.message);
      return false;
    }
  };
  return (
    <motion.div
      className="home-page login-page"
      initial={{ scale: 1, opacity: 1 }}
      animate={
        !showDashboard ? { scale: 1, opacity: 1 } : { scale: 1, opacity: 0 }
      }
    >
      <motion.div className="dashboard-resposnive">
        <img src={logo} alt="" />
        <p>Khadamat Admin Panel Is Available Only On Bigger Screens !</p>
      </motion.div>

      <div className="black-overlay"></div>
      <div className="header">
        <div className="logo-container">
          <p>Korsaa</p>
        </div>
      </div>

      <div className="login-form">
        <img src={logo} alt="" />
        <p className="small-text">Welcome Again</p>
        <h2>LOGIN</h2>
        <p className="small-text">
          Veuillez vous athentifier pour accder  votre espace Admin
        </p>
        <form onSubmit={HandleLogin}>
          <div className="form-group">
            <label htmlFor="email">Email:</label>
            <input
              type="email"
              id="email"
              name="email"
              placeholder="Votre email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              required
            />
          </div>

          <div className="form-group ">
            <label htmlFor="password">Password:</label>
            <input
              type="password"
              id="password"
              placeholder="Votre Mot De Passe"
              name="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              required
            />
          </div>
          <div className="msg-container">
            <motion.p
              initial={{ scale: 0, opacity: 0 }}
              animate={
                !showMsg ? { scale: 0, opacity: 0 } : { scale: 1, opacity: 1 }
              }
            >
              Corrdon erron, Veuillez ressayer
            </motion.p>
          </div>
          <div className="action-group">
            <div className="btn-container">
              <p>Mot de passe Oubli ?</p>
            </div>
            <div
              className="btn-container"
              onClick={(event) => HandleLogin(event)}
            >
              <motion.p
                initial={{ scale: 1, opacity: 1 }}
                animate={
                  loading ? { scale: 0, opacity: 0 } : { scale: 1, opacity: 1 }
                }
              >
                Se Connecter
              </motion.p>
              <motion.img
                className="loading"
                initial={{ scale: 1, opacity: 1 }}
                animate={
                  !loading ? { scale: 0, opacity: 0 } : { scale: 1, opacity: 1 }
                }
                src={loadingIcon}
              />
            </div>
          </div>
        </form>
      </div>

      <div className="colored-back"></div>
      <div className="colored-back2"></div>
    </motion.div>
  );
};

export default Home;
