import "../styles/TransporterForm.css";

import { motion } from "framer-motion";

import React, { useState, useEffect } from "react";

import exit from "../asstes/dashboard/exit.png";
import loadingIcon from "../asstes/loadingIcon.gif";

import { getDatabase, ref, set, push, get, update } from "firebase/database";

import { auth } from "../Components/database_config/firebaseConfig"; // Import auth
import { signInWithEmailAndPassword } from "firebase/auth"; // Import Firebase Auth methods

function PasswordUpdate({
  setUpdateServiceView,
  close,
  setShowPasswordUpdateLast,
}) {
  const [name, setName] = useState("");
  const [price, setPrice] = useState("");

  const [loading, setLoading] = useState(false);
  const [showMsg, setShowMsg] = useState(false);

  const clear = () => {
    setName("");
    setPrice("");
  };

  const submit = async () => {
    setLoading(true);
    try {
      // Firebase login with email and password
      const userCredential = await signInWithEmailAndPassword(
        auth,
        name,
        price
      );


      setTimeout(() => {
        
        clear();
        setShowPasswordUpdateLast(true);
      }, 3000);
    } catch (error) {
      console.error("Error logging in:", error.message);
      setShowMsg(true);

      setTimeout(() => setShowMsg(false), 5000);
    } finally {

        setTimeout(() => {
            setLoading(false);
            
        }, 4000);
    }
  };

  return (
    <motion.div
      className="transporter-Form password-update-form"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1, transition: { delay: 2 } }}
    >
      <div
        className="exit-btn"
        onClick={() => {
          close();
        }}
      >
        <img src={exit} alt="" />
      </div>

      <div className="centre-text">
        <p>Update Admin User Form</p>
        <p className="my-20 grey">
          For Your Security, You Need To Log In To The Admin Account.
        </p>
      </div>

      <div className="form-grp">
        <div className="form-input">
          <p>Username</p>
          <input
            type="text"
            placeholder="Admin Username Here..."
            value={name}
            onChange={(e) => {
              setName(e.target.value);
            }}
          />
        </div>

        <div className="form-input">
          <p>Password</p>
          <input
            type="password"
            placeholder="Admin Password Here..."
            value={price}
            onChange={(e) => {
              setPrice(e.target.value);
            }}
          />
        </div>
      </div>

      <div className="form-actions">
        <div
          className="form-action bkBlue update-btn"
          onClick={() => {
            submit();
          }}
        >
          {" "}
          <motion.p
          className="btn white"
            initial={{ scale: 1, opacity: 1 }}
            animate={
              loading ? { scale: 0, opacity: 0 } : { scale: 1, opacity: 1 }
            }
          >
            Confirmer
          </motion.p>
          <motion.img
            className="loading"
            initial={{ scale: 0, opacity: 0 }}
            animate={
              !loading ? { scale: 0, opacity: 0 } : { scale: 1, opacity: 1 }
            }
            src={loadingIcon}
          />
        </div>
      </div>

      <motion.p
        initial={{ opacity: 0 }}
        animate={showMsg ? { opacity: 1 } : { opacity: 0 }}
        className="error-msg"
      >
        Login Failed, Please Enter The Admin Informations Correctly.
      </motion.p>
    </motion.div>
  );
}

export default PasswordUpdate;
