import "../styles/TransporterForm.css";

import { motion } from "framer-motion";

import React, { useState, useEffect } from "react";

import exit from "../asstes/dashboard/exit.png";
import actions from "../asstes/dashboard/actions.png";
import arrow from "../asstes/dashboard/arrow.png";

import loadingIcon from "../asstes/dashboard/greenLoading.gif";

import { useStateWithCallbackLazy } from "use-state-with-callback";

import { getDatabase, ref, set, push, get } from "firebase/database";

import DatePicker from "react-datepicker";

function OrderPage({
  setShowMyMap,
  setShowTranspDetails,
  showTranspDetails,
  setSelectedTranspDetail,
  selectedTranspDetail,
  updateOrderView,
  setUpdateOrderView,
  setShowDismissDetailOverlay,
  setShowCancelDetailOverlay,
  choiceTransoporter,
  setChoiceTransoporter,
  setShowTransporterInfos,
  anime,
  selectOrderTransoprter,
  setSelectTranspOverlay,
  showTransporterInfos,
  setShowOrdersOverlay,
  setSelectedOrder,
  setPropAll,
  setPropFilter,
  setPropPres,
}) {
  const [selectedStateFilter, setSelectedStateFilter] = useState(0);
  const [showSelectOption1, setShowSelect1] = useState(false);

  const initSelected = [true, true, true, true, true, true, true, true];
  const [selectedField, setSelectedField] = useState(initSelected);

  const [presAllOrders, setPresAllOrders] = useStateWithCallbackLazy([]);

  const [allOrders, setAllOrders] = useState([]);
  const [filterOrders, setFilterOrders] = useState([]);

  const [allTransporters, setAllTransporters] = useState([]);
  const [filteTransporters, setFilterTransporters] = useState([]);

  const [selectedDateAnim, setSelectedDateAnim] = useState(0);
  const [showOrderAction, setShowOrderAction] = useState(-1);
  const [selectedOrderToUpdate, setSelectedOrderToUpdate] = useState(null);
  const [showSelectOption2, setShowSelect2] = useState(false);

  const [lodaing, setLoading] = useState(false);

  useEffect(() => {
    setPropFilter(filterOrders);
  }, [filterOrders]);

  useEffect(() => {
    setPropAll(allOrders);
  }, [allOrders]);

  useEffect(() => {
    setPropPres(presAllOrders);
  }, [presAllOrders]);

  const [calandarDate, setCalandarDate] = useState(new Date());

  const [rechTransp, setRechTransp] = useState("");

  function compare(a, b) {
    if (a.price < b.price) {
      console.log("cc");
      return 1;
    }
    if (a.price > b.price) {
      return -1;
    }
    return 0;
  }

  function compareName(a, b) {
    console.log("compareName");
    if (
      a.client_name != undefined &&
      b.client_name != undefined &&
      a.client_name.toLowerCase() > b.client_name.toLowerCase()
    ) {
      console.log("cc");
      return -1;
    }
    if (
      a.client_name != undefined &&
      b.client_name != undefined &&
      a.client_name.toLowerCase() < b.client_name.toLowerCase()
    ) {
      console.log("cc");
      return 1;
    }
    return 0;
  }

  const getPrevDate = (i) => {
    var date = new Date();
    date.setDate(date.getDate() - i);
    var res = {
      day: date.toString().split(" ")[0],
      date: date.toString().split(" ")[2],
    };
    return res;
  };

  const formatDate = (date) => {
    var dd = String(date.getDate()).padStart(2, "0");
    var mm = String(date.getMonth() + 1).padStart(2, "0"); //January is 0!
    var yyyy = date.getFullYear();

    var res = dd + "/" + mm + "/" + yyyy;
    return res;
  };

  const filterTransoprterOnSelect = (payment, from) => {
    console.log(payment + " " + from);

    var res = [];
    allTransporters.map((item) => {
      if (item.payment_method == payment && item.city == from) {
        res.push(item);
      }
    });

    setChoiceTransoporter(res);
  };

  const updateTranspRech = () => {
    var res = [];
    if (rechTransp === "") {
      setChoiceTransoporter([]);
    } else {
      allTransporters.map((item) => {
        if (item.name.toLowerCase().includes(rechTransp.toLowerCase())) {
          res.push(item);
        }
      });

      setChoiceTransoporter(res);
    }
  };

  const getOrdersByDate = async (date) => {
    //must change : store all orders in a var

    console.log("getOrdersByDate..........................................");

    console.log("getting all orders..." + date);
    const db = getDatabase();
    const shipments = ref(db, "offers");
    const snapshot = await get(shipments);

    const data = snapshot.val();
    const list = Object.entries(data);
    var clientorders = [];
    var index = 1;

    setLoading(true);
    for (let index = 0; index < list.length; index++) {
      var element = list[index];

      console.log(element[1].date_posted);
      var formatedDate =
        date.split("/")[2] +
        "-" +
        date.split("/")[1] +
        "-" +
        date.split("/")[0];

      console.log(formatedDate);
      if (formatedDate === element[1].date_posted) {
        console.log("find ............");
        console.log(element);
        var client = await getClientById(element[1].client_id);

        var service = await getServiceById(element[1].service_id);

        var worker = await getWorkerById(element[1].worker_id);
        console.log(worker[1].name);

        let it = element[1];
        it.client_name = client[1].name;
        it.lng = element[1].lng;
        it.lat = element[1].lat;

        it.service_name = service[1].name;
        it.worker_name = worker[1].name;

        it.setInf = (obj, filtOrd, allOrd, presOrd) => {
          updateItem(it, obj, filtOrd, allOrd, presOrd);
        };

        clientorders.push(it);
      }
    }

    setLoading(false);
    console.log(clientorders);
    setAllOrders(clientorders);
    setFilterOrders(clientorders);
    setPresAllOrders(clientorders);
  };

  const sortBy = (list, prop) => {
    var res = [];
    if (list === "orders") {
      console.log("sort by");
      if (prop === "name") {
        res = filterOrders;
        res.sort(compareName);
        console.log("name");
        setFilterOrders(res);
      } else {
        res = filterOrders;
        res.sort(compare);
        console.log("price");
        setFilterOrders(res);
      }
    } else {
      if (list === "clients") {
      } else {
      }
    }
  };

  const filterByState = (filter) => {
    var filterRes = [];
    if (filter == "a") {
      setFilterOrders(allOrders);
    } else {
      allOrders.map((item) => {
        if (item.status === filter) {
          filterRes.push(item);
        }
      });

      setFilterOrders(filterRes);
    }
  };

  const getAllOrders = async () => {
    console.log("getting all orders...");
    const db = getDatabase();
    const shipments = ref(db, "offers");
    const snapshot = await get(shipments);

    const data = snapshot.val();
    const list = Object.entries(data);
    var clientorders = [];
    var index = 1;

    setLoading(true);
    for (let index = 0; index < list.length; index++) {
      var element = list[index];

      console.log(element);

      var client = await getClientById(element[1].client_id);

      var service = await getServiceById(element[1].service_id);

      var worker = await getWorkerById(element[1].worker_id);
      console.log(worker[1].name);

      let it = element[1];
      it.client_name = client[1].name;
      it.lng = element[1].lng;
      it.lat = element[1].lat;

      it.service_name = service[1].name;
      it.worker_name = worker[1].name;

      it.setInf = (obj, filtOrd, allOrd, presOrd) => {
        updateItem(it, obj, filtOrd, allOrd, presOrd);
      };

      clientorders.push(it);

      if (index == list.length - 2) {
        setLoading(false);
      }
    }

    setAllOrders(clientorders);
    setFilterOrders(clientorders);
    setPresAllOrders(clientorders);
  };

  const updateItem = (it, obj, filtOrd, allOrd, presOrd) => {
    console.log(filtOrd);
    const updatedItems = filtOrd.map((item) => {
      if (item.id === it.id) {
        Object.keys(obj).forEach((key, ind) => {
          item[key] = obj[key];
        });
      }
      return item;
    });
    console.log(updatedItems);
    setFilterOrders(updatedItems);
  };

  const getClientById = async (id) => {
    const db = getDatabase();
    const shipments = ref(db, "clients");
    let ss = await get(shipments);

    const data = ss.val();
    const list = Object.entries(data);
    for (let i = 0; i < list.length; i++) {
      let element = list[i];
      if (element[0] == id) {
        return element;
      }
    }

    return "cc";
  };

  const getServiceById = async (id) => {
    const db = getDatabase();
    const shipments = ref(db, "services");
    let ss = await get(shipments);

    const data = ss.val();
    const list = Object.entries(data);
    for (let i = 0; i < list.length; i++) {
      let element = list[i];
      if (element[0] == id) {
        return element;
      }
    }

    return "cc";
  };

  const getWorkerById = async (id) => {
    const db = getDatabase();
    const shipments = ref(db, "workers");
    let ss = await get(shipments);

    const data = ss.val();
    const list = Object.entries(data);
    for (let i = 0; i < list.length; i++) {
      let element = list[i];
      if (element[0] == id) {
        return element;
      }
    }

    return "cc";
  };

  const getAllTransporter = () => {
    const db = getDatabase();
    const shipments = ref(db, "workers");

    get(shipments).then((snapshot) => {
      const data = snapshot.val();
      const list = Object.entries(data);

      var transps = [];
      list.forEach((element) => {
        console.log("agent exsist");
        transps.push({
          id: element[0],
          name: element[1].name,
          city: element[1].city,
          location: element[1].location,
          email: element[1].email,
          payment_method: element[1].payment_method,
          phone: element[1].phone,
          state: element[1].status,
          vehicle_type: element[1].vehicle,
          date: "12/12/2023",
        });
      });

      setAllTransporters(transps);
      setChoiceTransoporter(transps);
    });
  };

  function getTodayDate() {
    const today = new Date();
    const year = today.getFullYear();
    const month = String(today.getMonth() + 1).padStart(2, "0"); // Months are zero-based
    const day = String(today.getDate()).padStart(2, "0");

    return `${year}-${month}-${day}`;
  }

  useEffect(() => {
    setUpdateOrderView(false);
  }, [updateOrderView]);

  useEffect(() => {
    setTimeout(() => {
      var date = new Date();
      date.setDate(date.getDate() - 0);
      getOrdersByDate(formatDate(date));
    }, 2000);

    getAllTransporter();

    window.addEventListener("click", function (e) {
      if (
        document.getElementById("select-option") != null &&
        !document.getElementById("select-option").contains(e.target)
      ) {
        setShowSelect1(false);
      }

      if (
        document.getElementById("select-option2") != null &&
        !document.getElementById("select-option2").contains(e.target)
      ) {
        setShowSelect2(false);
      }
    });
  }, []);

  return (
    <motion.div
      className="dashboard-orders orders-page"
      id="page2"
      initial={{ opacity: 0 }}
      animate={anime ? { opacity: 1, zIndex: 3 } : { opacity: 0, zIndex: 0 }}
    >
      <div className="left-orders">
        <div className="dash-page-title">
          <p>Khadamat</p>
          <p>My Orders</p>
        </div>

        <div className="orders-filters">
          <div
            onClick={() => {
              setSelectedStateFilter(0);
              filterByState("a");
            }}
          >
            <p>All</p>
            <motion.div
              className="selected-order-filter"
              initial={{ width: "100%" }}
              animate={
                selectedStateFilter == 0 ? { width: "100%" } : { width: 0 }
              }
            ></motion.div>
          </div>

          <div
            onClick={() => {
              setSelectedStateFilter(1);
              filterByState("waiting");
            }}
          >
            <p>Waiting</p>
            <motion.div
              className="selected-order-filter"
              initial={{ width: "100%" }}
              animate={
                selectedStateFilter == 1 ? { width: "100%" } : { width: 0 }
              }
            ></motion.div>
          </div>

          <div
            onClick={() => {
              setSelectedStateFilter(2);
              filterByState("completed");
            }}
          >
            <p>Completed</p>
            <motion.div
              className="selected-order-filter"
              initial={{ width: "100%" }}
              animate={
                selectedStateFilter == 2 ? { width: "100%" } : { width: 0 }
              }
            ></motion.div>
          </div>

          <div
            onClick={() => {
              setSelectedStateFilter(5);
              filterByState("canceled");
            }}
          >
            <motion.div
              className="selected-order-filter"
              initial={{ width: "0%" }}
              animate={
                selectedStateFilter == 5 ? { width: "100%" } : { width: 0 }
              }
            ></motion.div>
            <p>Canceled</p>
          </div>
        </div>

        <div className="filters-container">
          <div
            className="filer-components"
            onClick={() => {
              setShowSelect1(true);
            }}
          >
            <div class="customize">
              <p>Customize</p>
            </div>

            <motion.div
              id="select-option"
              className="filter-select"
              initial={{ opacity: 0, top: "50%" }}
              animate={
                showSelectOption1
                  ? {
                      opacity: 1,
                      top: "120%",
                      transition: { ease: "backInOut" },
                    }
                  : {
                      opacity: 0,
                      top: "50%",
                      transition: { ease: "backInOut" },
                    }
              }
            >
              <motion.div
                className="filter-options"
                onClick={() => {
                  if (showSelectOption1) {
                    const arr = selectedField.map(function (item, index) {
                      if (index === 0) {
                        item = !item;
                      }
                      return item;
                    });

                    setSelectedField(arr);

                    console.log(selectedField);
                    console.log(selectedField[0]);
                  }
                }}
              >
                <p>ID</p>
                <motion.p
                  className="select-text-action"
                  animate={!selectedField[0] ? { opacity: 1 } : { opacity: 0 }}
                >
                  show
                </motion.p>

                <motion.p
                  className="select-text-action"
                  animate={selectedField[0] ? { opacity: 1 } : { opacity: 0 }}
                >
                  hide
                </motion.p>
              </motion.div>

              <motion.div
                className="filter-options"
                onClick={() => {
                  const arr = selectedField.map(function (item, index) {
                    if (index === 1) {
                      item = !item;
                    }
                    return item;
                  });

                  setSelectedField(arr);

                  console.log(selectedField);
                }}
              >
                <p>Client</p>
                <motion.p
                  className="select-text-action"
                  animate={!selectedField[1] ? { opacity: 1 } : { opacity: 0 }}
                >
                  show
                </motion.p>

                <motion.p
                  className="select-text-action"
                  animate={selectedField[1] ? { opacity: 1 } : { opacity: 0 }}
                >
                  hide
                </motion.p>
              </motion.div>

              <motion.div
                className="filter-options"
                animate={
                  showSelectOption1 ? { display: "flex" } : { display: "none" }
                }
                onClick={() => {
                  const arr = selectedField.map(function (item, index) {
                    if (index === 2) {
                      item = !item;
                    }
                    return item;
                  });

                  setSelectedField(arr);

                  console.log(selectedField);
                  console.log(selectedField[0]);
                }}
              >
                <p>Service</p>
                <motion.p
                  className="select-text-action"
                  animate={!selectedField[2] ? { opacity: 1 } : { opacity: 0 }}
                >
                  show
                </motion.p>

                <motion.p
                  className="select-text-action"
                  animate={selectedField[2] ? { opacity: 1 } : { opacity: 0 }}
                >
                  hide
                </motion.p>
              </motion.div>

              <motion.div
                className="filter-options"
                animate={
                  showSelectOption1 ? { display: "flex" } : { display: "none" }
                }
                onClick={() => {
                  const arr = selectedField.map(function (item, index) {
                    if (index === 3) {
                      item = !item;
                    }
                    return item;
                  });

                  setSelectedField(arr);

                  console.log(selectedField);
                  console.log(selectedField[0]);
                }}
              >
                <p>Wilaya</p>
                <motion.p
                  className="select-text-action"
                  animate={!selectedField[3] ? { opacity: 1 } : { opacity: 0 }}
                >
                  show
                </motion.p>

                <motion.p
                  className="select-text-action"
                  animate={selectedField[3] ? { opacity: 1 } : { opacity: 0 }}
                >
                  hide
                </motion.p>
              </motion.div>
              <motion.div
                className="filter-options"
                animate={
                  showSelectOption1 ? { display: "flex" } : { display: "none" }
                }
                onClick={(event) => {
                  const arr = selectedField.map(function (item, index) {
                    if (index === 4) {
                      item = !item;
                    }
                    return item;
                  });

                  setSelectedField(arr);

                  console.log(selectedField);
                  console.log(selectedField[0]);
                }}
              >
                <p>Commune</p>
                <motion.p
                  className="select-text-action"
                  animate={!selectedField[4] ? { opacity: 1 } : { opacity: 0 }}
                >
                  show
                </motion.p>

                <motion.p
                  className="select-text-action"
                  animate={selectedField[4] ? { opacity: 1 } : { opacity: 0 }}
                >
                  hide
                </motion.p>
              </motion.div>
              <motion.div
                className="filter-options"
                animate={
                  showSelectOption1 ? { display: "flex" } : { display: "none" }
                }
                onClick={() => {
                  const arr = selectedField.map(function (item, index) {
                    if (index === 5) {
                      item = !item;
                    }
                    return item;
                  });

                  setSelectedField(arr);

                  console.log(selectedField);
                  console.log(selectedField[0]);
                }}
              >
                <p>Status</p>
                <motion.p
                  className="select-text-action"
                  animate={!selectedField[5] ? { opacity: 1 } : { opacity: 0 }}
                >
                  show
                </motion.p>

                <motion.p
                  className="select-text-action"
                  animate={selectedField[5] ? { opacity: 1 } : { opacity: 0 }}
                >
                  hide
                </motion.p>
              </motion.div>
            </motion.div>
          </div>
          <div
            className="filer-components"
            onClick={(e) => {
              setShowSelect2(true);
            }}
          >
            <p>Sort By</p>

            <motion.div
              id="select-option2"
              className="filter-select"
              initial={{ opacity: 0, top: "50%" }}
              animate={
                showSelectOption2
                  ? {
                      opacity: 1,
                      top: "120%",
                      transition: { ease: "backInOut" },
                    }
                  : {
                      opacity: 0,
                      top: "50%",
                      transition: { ease: "backInOut" },
                    }
              }
            >
              <div
                className="filter-options"
                onClick={(e) => {
                  e.preventDefault();
                  sortBy("orders", "name");
                }}
              >
                <p>Client Name</p>
              </div>
            </motion.div>
          </div>
        </div>

        <div className="orders-list" id="orders-list">
          <motion.div
            className="loading-order"
            animate={lodaing ? { opacity: 1 } : { opacity: 0 }}
          >
            <img src={loadingIcon} />
          </motion.div>
          <motion.div className="order-list-item">
            <motion.p
              initial={{ opacity: 1 }}
              animate={
                selectedField[0]
                  ? { opacity: 1 }
                  : { opacity: 0, display: "none" }
              }
            >
              Id
            </motion.p>
            <motion.p
              initial={{ opacity: 1 }}
              animate={
                selectedField[1]
                  ? { opacity: 1 }
                  : { display: "none", opacity: 0 }
              }
            >
              Client
            </motion.p>
            <motion.p
              initial={{ opacity: 1 }}
              animate={
                selectedField[2]
                  ? { opacity: 1 }
                  : { display: "none", opacity: 0 }
              }
            >
              Service
            </motion.p>
            <motion.p
              initial={{ opacity: 1 }}
              animate={
                selectedField[3]
                  ? { opacity: 1 }
                  : { display: "none", opacity: 0 }
              }
            >
              Wilaya
            </motion.p>
            <motion.p
              initial={{ opacity: 1 }}
              animate={
                selectedField[4]
                  ? { opacity: 1 }
                  : { display: "none", opacity: 0 }
              }
            >
              Commune
            </motion.p>
            <motion.p
              initial={{ opacity: 1 }}
              animate={
                selectedField[5]
                  ? { opacity: 1 }
                  : { display: "none", opacity: 0 }
              }
            >
              Status
            </motion.p>
          </motion.div>
          {filterOrders.length == 0
            ? filterOrders.map((item, index) => {
                return (
                  <div>
                    <div className="order-list-item">
                      <motion.p
                        initial={{ opacity: 1 }}
                        animate={
                          selectedField[0]
                            ? { opacity: 1 }
                            : { opacity: 0, display: "none" }
                        }
                      >
                        {index}
                      </motion.p>
                      <motion.p
                        initial={{ opacity: 1 }}
                        animate={
                          selectedField[1]
                            ? { opacity: 1 }
                            : { display: "none", opacity: 0 }
                        }
                      >
                        {item.client_name}
                      </motion.p>
                      <motion.p
                        initial={{ opacity: 1 }}
                        animate={
                          selectedField[2]
                            ? { opacity: 1 }
                            : { display: "none", opacity: 0 }
                        }
                      >
                        {item.service_name}
                      </motion.p>
                      <motion.p
                        className="ellipsis"
                        initial={{ opacity: 1 }}
                        animate={
                          selectedField[3]
                            ? { opacity: 1 }
                            : { display: "none", opacity: 0 }
                        }
                      >
                        {item.wilaya}
                      </motion.p>
                      <motion.p
                        initial={{ opacity: 1 }}
                        animate={
                          selectedField[4]
                            ? { opacity: 1 }
                            : { display: "none", opacity: 0 }
                        }
                      >
                        {item.commune}
                      </motion.p>
                      <motion.p
                        initial={{ opacity: 1 }}
                        animate={
                          selectedField[5]
                            ? { opacity: 1 }
                            : { display: "none", opacity: 0 }
                        }
                      >
                        {item.status}
                      </motion.p>

                      <div
                        className="action"
                        onClick={() => {
                          setShowOrderAction(index);
                          setSelectedOrder(item);
                        }}
                      >
                        <img src={actions} alt="" />
                      </div>
                    </div>

                    <motion.div
                      className="order-list-item order-actions-container"
                      initial={{ height: "0", opacity: 0 }}
                      animate={
                        showOrderAction == index
                          ? {
                              height: "60px",
                              opacity: 1,
                              transition: { delay: 0 },
                            }
                          : {
                              height: "0",
                              opacity: 0,
                              transition: { delay: 0 },
                            }
                      }
                    >
                      <motion.div
                        className="order-action bkBlue"
                        initial={{ opacity: 0 }}
                        animate={
                          showOrderAction == index
                            ? { opacity: 1 }
                            : { opacity: 0 }
                        }
                        onClick={() => {
                          setSelectedOrderToUpdate(item);
                          setSelectedOrder(item);
                        }}
                      >
                        <p>Select Worker</p>
                      </motion.div>
                      <motion.div
                        className="order-action bkRed"
                        initial={{ opacity: 0 }}
                        animate={
                          showOrderAction == index
                            ? { opacity: 1 }
                            : { opacity: 0 }
                        }
                        onClick={() => {
                          setShowOrdersOverlay(true);
                          setSelectedOrder(item);
                        }}
                      >
                        <p>More details</p>
                      </motion.div>

                      <motion.div
                        className="order-action bkYellow"
                        initial={{ opacity: 0 }}
                        animate={
                          showOrderAction == index
                            ? { opacity: 1 }
                            : { opacity: 0 }
                        }
                        onClick={() => {
                          setSelectedOrder(item);
                          setShowCancelDetailOverlay(true);
                        }}
                      >
                        <p>Cancel</p>
                      </motion.div>

                      <motion.div
                        className="order-action bkBlue"
                        initial={{ opacity: 0 }}
                        animate={
                          showOrderAction == index
                            ? { opacity: 1 }
                            : { opacity: 0 }
                        }
                        onClick={() => {
                          setShowMyMap(true);
                        }}
                      >
                        <p>Show In Map</p>
                      </motion.div>
                    </motion.div>
                  </div>
                );
              })
            : filterOrders.map((item, index) => {
                return (
                  <div>
                    <div className="order-list-item">
                      <motion.p
                        initial={{ opacity: 1 }}
                        animate={
                          selectedField[0]
                            ? { opacity: 1 }
                            : { opacity: 0, display: "none" }
                        }
                      >
                        {index + 1}
                      </motion.p>
                      <motion.p
                        initial={{ opacity: 1 }}
                        animate={
                          selectedField[1]
                            ? { opacity: 1 }
                            : { display: "none", opacity: 0 }
                        }
                      >
                        {item.client_name}
                      </motion.p>
                      <motion.p
                        initial={{ opacity: 1 }}
                        animate={
                          selectedField[2]
                            ? { opacity: 1 }
                            : { display: "none", opacity: 0 }
                        }
                      >
                        {item.service_name}
                      </motion.p>
                      <motion.p
                        className="ellipsis"
                        initial={{ opacity: 1 }}
                        animate={
                          selectedField[3]
                            ? { opacity: 1 }
                            : { display: "none", opacity: 0 }
                        }
                      >
                        {item.wilaya}
                        <motion.div className="mask"></motion.div>
                      </motion.p>
                      <motion.p
                        className="ellipsis"
                        initial={{ opacity: 1 }}
                        animate={
                          selectedField[4]
                            ? { opacity: 1 }
                            : { display: "none", opacity: 0 }
                        }
                      >
                        {item.commune}

                        <motion.div className="mask"></motion.div>
                      </motion.p>
                      <motion.p
                        initial={{ opacity: 1 }}
                        animate={
                          selectedField[5]
                            ? { opacity: 1 }
                            : { display: "none", opacity: 0 }
                        }
                      >
                        {item.status}
                      </motion.p>

                      <div
                        className="action"
                        onClick={() => {
                          setShowOrderAction(index);

                          setSelectedOrder(item);
                        }}
                      >
                        <img src={actions} alt="" />
                      </div>
                    </div>

                    <motion.div
                      className="order-list-item order-actions-container"
                      initial={{ height: "0", opacity: 0 }}
                      animate={
                        showOrderAction == index
                          ? {
                              height: "60px",
                              opacity: 1,
                              transition: { delay: 0 },
                            }
                          : {
                              height: "0",
                              opacity: 0,
                              transition: { delay: 0 },
                            }
                      }
                    >
                      <motion.div
                        className="order-action bkBlue"
                        initial={{ opacity: 0 }}
                        animate={
                          showOrderAction == index
                            ? { opacity: 1 }
                            : { opacity: 0 }
                        }
                        onClick={() => {
                          setSelectedOrderToUpdate(item);
                          setSelectedOrder(item);
                        }}
                      >
                        <p>Select Worker</p>
                      </motion.div>
                      <motion.div
                        className="order-action bkRed"
                        initial={{ opacity: 0 }}
                        animate={
                          showOrderAction == index
                            ? { opacity: 1 }
                            : { opacity: 0 }
                        }
                        onClick={() => {
                          setShowOrdersOverlay(true);
                          setSelectedOrder(item);
                        }}
                      >
                        <p>More Details</p>
                      </motion.div>

                      <motion.div
                        className="order-action bkred"
                        initial={{ opacity: 0 }}
                        animate={
                          showOrderAction == index
                            ? { opacity: 1 }
                            : { opacity: 0 }
                        }
                        onClick={() => {
                          setSelectedOrder(item);
                          setShowCancelDetailOverlay(true);
                        }}
                      >
                        <p>Cancel</p>
                      </motion.div>

                      <motion.div
                        className="order-action bkBlue"
                        initial={{ opacity: 0 }}
                        animate={
                          showOrderAction == index
                            ? { opacity: 1 }
                            : { opacity: 0 }
                        }
                        onClick={() => {
                          setShowMyMap(true);
                        }}
                      >
                        <p>Show In Map</p>
                      </motion.div>
                    </motion.div>
                  </div>
                );
              })}
        </div>
      </div>
      <div className="right-orders">
        <div className="date-picker-container">
          <input
            type="date"
            className="date-container"
            value={calandarDate}
            onChange={(e) => {
              setCalandarDate(e.target.value);
              setSelectedDateAnim(8);
              getOrdersByDate(
                e.target.value.split("-")[2] +
                  "/" +
                  e.target.value.split("-")[1] +
                  "/" +
                  e.target.value.split("-")[0]
              );
            }}
          />
        </div>

        <div className="date-items-list">
          <motion.div
            className="date-item"
            animate={
              selectedDateAnim == 6
                ? { backgroundColor: "#2fd95f", color: "white" }
                : { backgroundColor: "transparent" }
            }
            onClick={() => {
              setSelectedDateAnim(6);
              var date = new Date();
              date.setDate(date.getDate() - 6);

              const today = new Date();
              const defaultValue = new Date(today).toISOString().split("T")[0];
              setCalandarDate(defaultValue);

              getOrdersByDate(formatDate(date));
            }}
          >
            <p>{getPrevDate(6).day}</p>
            <p>{getPrevDate(6).date}</p>
          </motion.div>
          <motion.div
            className="date-item"
            animate={
              selectedDateAnim == 5
                ? { backgroundColor: "#2fd95f", color: "white" }
                : { backgroundColor: "transparent" }
            }
            onClick={() => {
              setSelectedDateAnim(5);
              var date = new Date();
              date.setDate(date.getDate() - 5);
              getOrdersByDate(formatDate(date));

              const today = new Date();
              const defaultValue = new Date(today).toISOString().split("T")[0];
              setCalandarDate(defaultValue);
            }}
          >
            <p>{getPrevDate(5).day}</p>
            <p>{getPrevDate(5).date}</p>
          </motion.div>
          <motion.div
            className="date-item"
            animate={
              selectedDateAnim == 4
                ? { backgroundColor: "#2fd95f", color: "white" }
                : { backgroundColor: "transparent" }
            }
            onClick={() => {
              setSelectedDateAnim(4);
              var date = new Date();
              date.setDate(date.getDate() - 4);
              getOrdersByDate(formatDate(date));

              const today = new Date();
              const defaultValue = new Date(today).toISOString().split("T")[0];
              setCalandarDate(defaultValue);
            }}
          >
            <p>{getPrevDate(4).day}</p>
            <p>{getPrevDate(4).date}</p>
          </motion.div>
          <motion.div
            className="date-item"
            animate={
              selectedDateAnim == 3
                ? { backgroundColor: "#2fd95f", color: "white" }
                : { backgroundColor: "transparent" }
            }
            onClick={() => {
              setSelectedDateAnim(3);
              var date = new Date();
              date.setDate(date.getDate() - 3);
              console.log(formatDate(date));
              getOrdersByDate(formatDate(date));

              const today = new Date();
              const defaultValue = new Date(today).toISOString().split("T")[0];
              setCalandarDate(defaultValue);
            }}
          >
            <p>{getPrevDate(3).day}</p>
            <p>{getPrevDate(3).date}</p>
          </motion.div>
          <motion.div
            className="date-item"
            animate={
              selectedDateAnim == 2
                ? { backgroundColor: "#2fd95f", color: "white" }
                : { backgroundColor: "transparent" }
            }
            onClick={() => {
              setSelectedDateAnim(2);
              var date = new Date();
              date.setDate(date.getDate() - 2);
              console.log(formatDate(date));
              getOrdersByDate(formatDate(date));

              const today = new Date();
              const defaultValue = new Date(today).toISOString().split("T")[0];
              setCalandarDate(defaultValue);
            }}
          >
            <p>{getPrevDate(2).day}</p>
            <p>{getPrevDate(2).date}</p>
          </motion.div>
          <motion.div
            className="date-item"
            animate={
              selectedDateAnim == 1
                ? { backgroundColor: "#2fd95f", color: "white" }
                : { backgroundColor: "transparent" }
            }
            onClick={() => {
              setSelectedDateAnim(1);
              var date = new Date();
              date.setDate(date.getDate() - 1);
              console.log(formatDate(date));
              getOrdersByDate(formatDate(date));

              const today = new Date();
              const defaultValue = new Date(today).toISOString().split("T")[0];
              setCalandarDate(defaultValue);
            }}
          >
            <p>{getPrevDate(1).day}</p>
            <p>{getPrevDate(1).date}</p>
          </motion.div>
          <motion.div
            className="date-item"
            animate={
              selectedDateAnim == 0
                ? { backgroundColor: "#2fd95f", color: "white" }
                : { backgroundColor: "transparent" }
            }
            onClick={() => {
              setSelectedDateAnim(0);
              var date = new Date();
              date.setDate(date.getDate());
              console.log(formatDate(date));
              getOrdersByDate(formatDate(date));

              const today = new Date();
              const defaultValue = new Date(today).toISOString().split("T")[0];
              setCalandarDate(defaultValue);
            }}
          >
            <p>{getPrevDate(0).day}</p>
            <p>{getPrevDate(0).date}</p>
          </motion.div>
        </div>

        <div className="transporter-side-list-container">
          <p>Worker</p>

          <div className="rech-container">
            <input
              type="text"
              placeholder="Worker name"
              value={rechTransp}
              onChange={(e) => {
                setRechTransp(e.target.value);
                updateTranspRech();
              }}
            />
          </div>
          <div className="filters-container">
            <motion.div
              className="filer-components"
              animate={
                choiceTransoporter.length == 0
                  ? { backgroundColor: "#2fd95f", color: "white" }
                  : { backgroundColor: "transparent", color: "black" }
              }
              onClick={() => {
                setChoiceTransoporter([]);
              }}
            >
              <p>All</p>
            </motion.div>
            <motion.div
              className="filer-components"
              animate={
                choiceTransoporter.length != 0
                  ? { backgroundColor: "#2fd95f", color: "white" }
                  : { backgroundColor: "transparent", color: "black" }
              }
            >
              <p>Filtered</p>
            </motion.div>
          </div>

          <div className="transporter-side-list">
            {choiceTransoporter.length != 0
              ? choiceTransoporter.map((item, index) => {
                  return (
                    <div>
                      <div className="transporter-item">
                        <div className="left-transporter">
                          <div className="transporter-icon">
                            <img src={arrow} alt="" />
                          </div>

                          <div className="transporter-text">
                            <p>{item.name}</p>
                            <p className="small-text">Khadamat Worker</p>
                          </div>
                        </div>

                        <div
                          className="transporter-action"
                          onClick={() => {
                            setShowTransporterInfos(index);
                          }}
                        >
                          <img src={actions} alt="" />
                        </div>
                      </div>

                      <motion.div
                        className="transporter-infos"
                        initial={{ height: "0", opacity: 0 }}
                        animate={
                          showTransporterInfos == index
                            ? { height: "60px", opacity: 1 }
                            : { height: "0", opacity: 0 }
                        }
                      >
                        <motion.div
                          className="transporter-infos-action"
                          initial={{ scale: "0", opacity: 0 }}
                          animate={
                            showTransporterInfos == index
                              ? { scale: 1, opacity: 1 }
                              : { scale: 0, opacity: 0 }
                          }
                          onClick={() => {
                            setShowTranspDetails(true);
                            setSelectedTranspDetail(item);
                          }}
                        >
                          <p>More Info</p>
                        </motion.div>

                        <motion.div
                          className="transporter-infos-action"
                          id="select-tranps-btn"
                          initial={{ scale: "0", opacity: 0 }}
                          animate={
                            showTransporterInfos == index &&
                            selectedOrderToUpdate != null
                              ? { scale: 1, opacity: 1 }
                              : { scale: 0, opacity: 0 }
                          }
                          onClick={() => {
                            setSelectTranspOverlay(true);
                            selectOrderTransoprter(item);
                          }}
                        >
                          <p>Select</p>
                        </motion.div>
                      </motion.div>
                    </div>
                  );
                })
              : allTransporters.map((item, index) => {
                  return (
                    <div>
                      <div className="transporter-item">
                        <div className="left-transporter">
                          <div className="transporter-icon">
                            <img src={arrow} alt="" />
                          </div>

                          <div className="transporter-text">
                            <p>{item.name}</p>
                            <p className="small-text">Korssa Transporter</p>
                          </div>
                        </div>

                        <div
                          className="transporter-action"
                          onClick={() => {
                            setShowTransporterInfos(index);
                          }}
                        >
                          <img src={actions} alt="" />
                        </div>
                      </div>

                      <motion.div
                        className="transporter-infos"
                        initial={{ height: "0", opacity: 0 }}
                        animate={
                          showTransporterInfos == index
                            ? { height: "60px", opacity: 1 }
                            : { height: "0", opacity: 0 }
                        }
                      >
                        <motion.div
                          className="transporter-infos-action"
                          initial={{ scale: "0", opacity: 0 }}
                          animate={
                            showTransporterInfos == index
                              ? { scale: 1, opacity: 1 }
                              : { scale: 0, opacity: 0 }
                          }
                          onClick={() => {
                            setShowTranspDetails(true);
                            setSelectedTranspDetail(item);
                          }}
                        >
                          <p>More Info</p>
                        </motion.div>

                        <motion.div
                          className="transporter-infos-action"
                          id="select-tranps-btn"
                          initial={{ scale: "0", opacity: 0 }}
                          animate={
                            showTransporterInfos == index &&
                            selectedOrderToUpdate != null
                              ? { scale: 1, opacity: 1 }
                              : { scale: 0, opacity: 0 }
                          }
                          onClick={() => {
                            setSelectTranspOverlay(true);
                            selectOrderTransoprter(item);
                          }}
                        >
                          <p>Select</p>
                        </motion.div>
                      </motion.div>
                    </div>
                  );
                })}
          </div>
        </div>
      </div>
    </motion.div>
  );
}

export default OrderPage;
