import "../styles/dashboard.css";

import { motion } from "framer-motion";

import React, { useState, useEffect } from "react";

import exit from "../asstes/dashboard/exit.png";

import { getDatabase, ref, remove } from "firebase/database";

function DeleteWorkerConf({
  anime,
  worker,
  close,
  approveTransp,
  setUpdateTranspView,
}) {
  const deleteWorker = async () => {
    if (!worker || !worker.id) {
      console.error("Worker data is missing.");
      return;
    }

    try {
      const db = getDatabase(); // Initialize database
      const workerRef = ref(db, `workers/${worker.id}`); // Path to the worker in the database

      console.error("deleting worker:");
      await remove(workerRef); // Remove the worker
      console.log(`Worker with ID ${worker.id} deleted successfully.`);
      setUpdateTranspView((prev) => !prev); // Update the parent state to refresh the view
      close(); // Close the confirmation dialog
    } catch (error) {
      console.error("Error deleting worker:", error.message);
    }
  };

  return worker != null ? (
    <motion.div
      className="select-transporter-overlay"
      initial={{ scale: 0, opacity: 0, display: "none" }}
      animate={
        anime
          ? { scale: 1, display: "flex", opacity: 1 }
          : { scale: 0.5, opacity: 0, transition: { ease: "easeInOut" } }
      }
      exit={{ display: "none" }}
      onClick={(event) => {
        event.preventDefault();
      }}
    >
      <div
        className="select-transporter-container"
        id="select-transporter"
        onClick={(event) => {
          event.preventDefault();
        }}
      >
        <p>Confirmation</p>

        <p>
          Do you want to Delete Worker <span>{worker.name}</span> with id{" "}
          <span>{worker.id}</span> From The khadamat DataBase ?
        </p>

        <div className="conf-actions">
          <div
            className="conf-action"
            onClick={() => {
              deleteWorker();
              close();
            }}
          >
            <p>Confirm</p>
          </div>
          <div
            className="conf-action"
            onClick={() => {
              close();
            }}
          >
            <p>Cancel</p>
          </div>
        </div>
      </div>
    </motion.div>
  ) : (
    <div></div>
  );
}

export default DeleteWorkerConf;
