import "../styles/TransporterForm.css";

import { motion } from "framer-motion";

import React, { useState, useEffect } from "react";

import exit from "../asstes/dashboard/exit.png";
import actions from "../asstes/dashboard/actions.png";
import arrow from "../asstes/dashboard/arrow.png";

import client from "../asstes/dashboard/client.png";

import { getDatabase, ref, set, onValue , get } from "firebase/database";
import {db} from "./database_config/firebaseConfig"

function ClientPage({
  setShowClinetDetails,
  setSelectedClientDetail,
  setShowDeleteClientConf,
  anime,
}) {
  const [showSelectOption1, setShowSelect1] = useState(false);

  const initSelected = [true, true, true, true, true, true, true, true];
  const [selectedField, setSelectedField] = useState(initSelected);

  const [filterOrders, setFilterOrders] = useState([]);

  const [showOrderAction, setShowOrderAction] = useState(-1);
  const [showSelectOption2, setShowSelect2] = useState(false);

  const [allClient, setAllClient] = useState([]);
  const [filterClinet, setFilterClinet] = useState([]);

  const [maleCount, setMaleCount] = useState(0);

  const [femaleCount, setFemaleCount] = useState(0);
  const [ordersCount, setOrderCount] = useState(0);

  function compare(a, b) {
    if (a.price < b.price) {
      console.log("cc");
      return 1;
    }
    if (a.price > b.price) {
      return -1;
    }
    return 0;
  }

  function compareName(a, b) {
    console.log("compareName");
    if (a.name.toUpperCase() < b.name.toUpperCase()) {
      console.log("cc");
      return -1;
    }
    if (a.name.toUpperCase() > b.name.toUpperCase()) {
      console.log("cc");
      return 1;
    }
    return 0;
  }

  const sortBy = (list, prop) => {
    var res = [];
    if (list === "orders") {
      console.log("sort by");
      if (prop === "name") {
        res = filterOrders;
        res.sort(compareName);
        console.log("name");
        setFilterOrders(res);
      } else {
        res = filterOrders;
        res.sort(compare);
        console.log("price");
        setFilterOrders(res);
      }
    } else {
      if (list === "clients") {
        console.log("sorting clients");
        if (filterClinet.length == 0) {
          res = allClient;
          res.sort(compareName);
          setAllClient(res);
        } else {
          res = filterClinet;
          res.sort(compareName);
          setFilterClinet(res);
        }
      } else {
      }
    }
  };

  const getAllClient = async () => {
    const db = getDatabase();
    const shipments = ref(db, "clients");

    var maleClient = 0;
    var femaleClient = 0;

    await get(shipments).then((snapshot) => {
      const data = snapshot.val();
      const list = Object.entries(data);
      var clients = [];
      var index = 1;
      list.forEach((element) => {
        console.log("client exsist");

        console.log(element);
        clients.push({
          id: element[0],
          name: element[1].name,
          email: element[1].email,
          phone: element[1].phone,
          age: element[1].age,
          gender: element[1].gender,
        });

        if (element[1].gender.toLowerCase() === "male") {
          maleClient++;
        } else {
          femaleClient++;
        }
        index++;
      });

      setAllClient(clients);
      setMaleCount(maleClient);
      setFemaleCount(femaleClient);
    });
  };

  useEffect(() => {
    const dataRef = ref(db, "clients"); // Specify the path to your data in the database

    // Set up a listener for real-time updates
    const unsubscribe = onValue(dataRef, (snapshot) => {
      if (snapshot.exists()) {
        getAllClient();
      } else {
      }
    });

    // Cleanup listener on component unmount
    return () => unsubscribe();
  }, []);

  useEffect(() => {
    console.log("clientpage.....");
    getAllClient();

    window.addEventListener("click", function (e) {
      if (
        document.getElementById("select-option") != null &&
        !document.getElementById("select-option").contains(e.target)
      ) {
        setShowSelect1(false);
      }

      if (
        document.getElementById("select-option2") != null &&
        !document.getElementById("select-option2").contains(e.target)
      ) {
        setShowSelect2(false);
      }
    });
  }, []);

  return (
    <motion.div
      className="dashboard-orders client-page"
      id="page4"
      initial={{ opacity: 0 }}
      animate={anime ? { opacity: 1, zIndex: 3 } : { opacity: 0, zIndex: 0 }}
    >
      <div className="client-page-content">
        <div className="clients-left">
          <div className="dash-page-title">
            <p>Khadamat</p>
            <p>My Clients</p>
          </div>

          <div className="orders-filters"></div>

          <div className="filters-container">
            <div
              className="filer-components"
              onClick={() => {
                setShowSelect1(true);
              }}
            >
              <p>Custumize</p>

              <motion.div
                id="select-option"
                className="filter-select"
                initial={{ opacity: 0, top: "50%" }}
                animate={
                  showSelectOption1
                    ? {
                        opacity: 1,
                        top: "120%",
                        transition: { ease: "backInOut" },
                      }
                    : {
                        opacity: 0,
                        top: "50%",
                        transition: { ease: "backInOut" },
                      }
                }
              >
                <div
                  className="filter-options"
                  onClick={() => {
                    if (showSelectOption1) {
                      const arr = selectedField.map(function (item, index) {
                        if (index === 0) {
                          item = !item;
                        }
                        return item;
                      });

                      setSelectedField(arr);

                      console.log(selectedField);
                      console.log(selectedField[0]);
                    }
                  }}
                >
                  <p>id</p>
                  <motion.p
                    className="select-text-action"
                    animate={
                      !selectedField[0] ? { opacity: 1 } : { opacity: 0 }
                    }
                  >
                    show
                  </motion.p>

                  <motion.p
                    className="select-text-action"
                    animate={selectedField[0] ? { opacity: 1 } : { opacity: 0 }}
                  >
                    hide
                  </motion.p>
                </div>
                <motion.div
                  className="filter-options"
                  animate={
                    showSelectOption1
                      ? { display: "flex" }
                      : { display: "none" }
                  }
                  onClick={() => {
                    const arr = selectedField.map(function (item, index) {
                      if (index === 1) {
                        item = !item;
                      }
                      return item;
                    });

                    setSelectedField(arr);

                    console.log(selectedField);
                  }}
                >
                  <p>Name</p>
                  <motion.p
                    className="select-text-action"
                    animate={
                      !selectedField[1] ? { opacity: 1 } : { opacity: 0 }
                    }
                  >
                    show
                  </motion.p>

                  <motion.p
                    className="select-text-action"
                    animate={selectedField[1] ? { opacity: 1 } : { opacity: 0 }}
                  >
                    hide
                  </motion.p>
                </motion.div>
                <motion.div
                  className="filter-options"
                  animate={
                    showSelectOption1
                      ? { display: "flex" }
                      : { display: "none" }
                  }
                  onClick={() => {
                    const arr = selectedField.map(function (item, index) {
                      if (index === 2) {
                        item = !item;
                      }
                      return item;
                    });

                    setSelectedField(arr);

                    console.log(selectedField);
                    console.log(selectedField[0]);
                  }}
                >
                  <p>Gendre</p>
                  <motion.p
                    className="select-text-action"
                    animate={
                      !selectedField[2] ? { opacity: 1 } : { opacity: 0 }
                    }
                  >
                    show
                  </motion.p>

                  <motion.p
                    className="select-text-action"
                    animate={selectedField[2] ? { opacity: 1 } : { opacity: 0 }}
                  >
                    hide
                  </motion.p>
                </motion.div>
                <motion.div
                  className="filter-options"
                  animate={
                    showSelectOption1
                      ? { display: "flex" }
                      : { display: "none" }
                  }
                  onClick={() => {
                    const arr = selectedField.map(function (item, index) {
                      if (index === 3) {
                        item = !item;
                      }
                      return item;
                    });

                    setSelectedField(arr);

                    console.log(selectedField);
                    console.log(selectedField[0]);
                  }}
                >
                  <p>Age</p>
                  <motion.p
                    className="select-text-action"
                    animate={
                      !selectedField[3] ? { opacity: 1 } : { opacity: 0 }
                    }
                  >
                    show
                  </motion.p>

                  <motion.p
                    className="select-text-action"
                    animate={selectedField[3] ? { opacity: 1 } : { opacity: 0 }}
                  >
                    hide
                  </motion.p>
                </motion.div>
                <motion.div
                  className="filter-options"
                  animate={
                    showSelectOption1
                      ? { display: "flex" }
                      : { display: "none" }
                  }
                  onClick={() => {
                    const arr = selectedField.map(function (item, index) {
                      if (index === 4) {
                        item = !item;
                      }
                      return item;
                    });

                    setSelectedField(arr);

                    console.log(selectedField);
                    console.log(selectedField[0]);
                  }}
                >
                  <p>Phone</p>
                  <motion.p
                    className="select-text-action"
                    animate={
                      !selectedField[4] ? { opacity: 1 } : { opacity: 0 }
                    }
                  >
                    show
                  </motion.p>

                  <motion.p
                    className="select-text-action"
                    animate={selectedField[4] ? { opacity: 1 } : { opacity: 0 }}
                  >
                    hide
                  </motion.p>
                </motion.div>
                <motion.div
                  className="filter-options"
                  animate={
                    showSelectOption1
                      ? { display: "flex" }
                      : { display: "none" }
                  }
                  onClick={() => {
                    const arr = selectedField.map(function (item, index) {
                      if (index === 5) {
                        item = !item;
                      }
                      return item;
                    });

                    setSelectedField(arr);

                    console.log(selectedField);
                    console.log(selectedField[0]);
                  }}
                >
                  <p>Email</p>
                  <motion.p
                    className="select-text-action"
                    animate={
                      !selectedField[5] ? { opacity: 1 } : { opacity: 0 }
                    }
                  >
                    show
                  </motion.p>

                  <motion.p
                    className="select-text-action"
                    animate={selectedField[5] ? { opacity: 1 } : { opacity: 0 }}
                  >
                    hide
                  </motion.p>
                </motion.div>
              </motion.div>
            </div>
            <div
              className="filer-components"
              onClick={() => {
                setShowSelect2(true);
              }}
            >
              <p>Sort By</p>

              <motion.div
                id="select-option2"
                className="filter-select"
                initial={{ opacity: 0, top: "50%" }}
                animate={
                  showSelectOption2
                    ? {
                        opacity: 1,
                        top: "120%",
                        transition: { ease: "backInOut" },
                      }
                    : {
                        opacity: 0,
                        top: "50%",
                        transition: { ease: "backInOut" },
                      }
                }
              >
                <div className="filter-options" onClick={() => {}}>
                  <p>Price</p>
                </div>

                <div
                  className="filter-options"
                  onClick={() => {
                    sortBy("clients", "name");
                  }}
                >
                  <p>Client Name</p>
                </div>
              </motion.div>
            </div>
          </div>

          <div className="orders-list clients-list">
            <motion.div className="order-list-item">
              <motion.p
                initial={{ opacity: 1 }}
                animate={
                  selectedField[0]
                    ? { opacity: 1 }
                    : { opacity: 0, display: "none" }
                }
              >
                Id
              </motion.p>
              <motion.p
                initial={{ opacity: 1 }}
                animate={
                  selectedField[1]
                    ? { opacity: 1 }
                    : { display: "none", opacity: 0 }
                }
              >
                Name
              </motion.p>
              <motion.p
                initial={{ opacity: 1 }}
                animate={
                  selectedField[2]
                    ? { opacity: 1 }
                    : { display: "none", opacity: 0 }
                }
              >
                Gendre
              </motion.p>

              <motion.p
                initial={{ opacity: 1 }}
                animate={
                  selectedField[3]
                    ? { opacity: 1 }
                    : { display: "none", opacity: 0 }
                }
              >
                Age
              </motion.p>
              <motion.p
                initial={{ opacity: 1 }}
                animate={
                  selectedField[4]
                    ? { opacity: 1 }
                    : { display: "none", opacity: 0 }
                }
              >
                Phone
              </motion.p>
              <motion.p
                initial={{ opacity: 1 }}
                animate={
                  selectedField[5]
                    ? { opacity: 1 }
                    : { display: "none", opacity: 0 }
                }
              >
                Email
              </motion.p>
            </motion.div>
            {filterClinet.length == 0
              ? allClient.map((item, index) => {
                  return (
                    <div>
                      <div className="order-list-item">
                        <motion.p
                          initial={{ opacity: 1 }}
                          animate={
                            selectedField[0]
                              ? { opacity: 1 }
                              : { opacity: 0, display: "none" }
                          }
                        >
                          {index + 1}
                        </motion.p>
                        <motion.p
                          initial={{ opacity: 1 }}
                          animate={
                            selectedField[1]
                              ? { opacity: 1 }
                              : { display: "none", opacity: 0 }
                          }
                        >
                          {item.name}
                        </motion.p>
                        <motion.p
                          initial={{ opacity: 1 }}
                          animate={
                            selectedField[2]
                              ? { opacity: 1 }
                              : { display: "none", opacity: 0 }
                          }
                        >
                          {item.gender}
                        </motion.p>
                        <motion.p
                          initial={{ opacity: 1 }}
                          animate={
                            selectedField[3]
                              ? { opacity: 1 }
                              : { display: "none", opacity: 0 }
                          }
                        >
                          {item.age}
                        </motion.p>

                        <motion.p
                          initial={{ opacity: 1 }}
                          animate={
                            selectedField[4]
                              ? { opacity: 1 }
                              : { display: "none", opacity: 0 }
                          }
                        >
                          {item.phone}
                        </motion.p>

                        <motion.p
                          className="ellipsis"
                          initial={{ opacity: 1 }}
                          animate={
                            selectedField[5]
                              ? { opacity: 1 }
                              : { display: "none", opacity: 0 }
                          }
                        >
                          {item.email}
                          <motion.div className="mask"></motion.div>
                        </motion.p>
                        <div
                          className="action"
                          onClick={() => {
                            setShowOrderAction(index);
                          }}
                        >
                          <img src={actions} alt="" />
                        </div>
                      </div>

                      <motion.div
                        className="order-list-item order-actions-container"
                        initial={{ height: "0", opacity: 0 }}
                        animate={
                          showOrderAction == index
                            ? {
                                height: "60px",
                                opacity: 1,
                                transition: { delay: 0 },
                              }
                            : {
                                height: "0",
                                opacity: 0,
                                transition: { delay: 0 },
                              }
                        }
                      >
                        <motion.div
                          className="order-action bkRed "
                          initial={{ opacity: 0 }}
                          animate={
                            showOrderAction == index
                              ? { opacity: 1 }
                              : { opacity: 0 }
                          }
                          onClick={() => {
                            setSelectedClientDetail(item);
                            setShowClinetDetails(true);
                          }}
                        >
                          <p>More details</p>
                        </motion.div>

                        <motion.div
                          className="order-action bkred "
                          initial={{ opacity: 0 }}
                          animate={
                            showOrderAction == index
                              ? { opacity: 1 }
                              : { opacity: 0 }
                          }
                          onClick={() => {
                            setSelectedClientDetail(item);
                            setShowDeleteClientConf(true);
                          }}
                        >
                          <p>Remove </p>
                        </motion.div>
                      </motion.div>
                    </div>
                  );
                })
              : filterClinet.map((item, index) => {
                  return (
                    <div>
                      <div className="order-list-item">
                        <motion.p
                          initial={{ opacity: 1 }}
                          animate={
                            selectedField[0]
                              ? { opacity: 1 }
                              : { opacity: 0, display: "none" }
                          }
                        >
                          {index + 1}
                        </motion.p>
                        <motion.p
                          initial={{ opacity: 1 }}
                          animate={
                            selectedField[1]
                              ? { opacity: 1 }
                              : { display: "none", opacity: 0 }
                          }
                        >
                          {item.name}
                        </motion.p>
                        <motion.p
                          initial={{ opacity: 1 }}
                          animate={
                            selectedField[2]
                              ? { opacity: 1 }
                              : { display: "none", opacity: 0 }
                          }
                        >
                          {item.phone}
                        </motion.p>

                        <motion.p
                          initial={{ opacity: 1 }}
                          animate={
                            selectedField[3]
                              ? { opacity: 1 }
                              : { display: "none", opacity: 0 }
                          }
                        >
                          {item.company_name}
                        </motion.p>
                        <motion.p
                          initial={{ opacity: 1 }}
                          animate={
                            selectedField[4]
                              ? { opacity: 1 }
                              : { display: "none", opacity: 0 }
                          }
                        >
                          {item.business_nature}
                        </motion.p>
                        <motion.p
                          className="ellipsis"
                          initial={{ opacity: 1 }}
                          animate={
                            selectedField[5]
                              ? { opacity: 1 }
                              : { display: "none", opacity: 0 }
                          }
                        >
                          {item.location}

                          <motion.div className="mask"></motion.div>
                        </motion.p>
                        <motion.p
                          className="state-container"
                          initial={{ opacity: 1 }}
                          animate={
                            selectedField[6]
                              ? { opacity: 1 }
                              : { display: "none", opacity: 0 }
                          }
                        >
                          <div
                            className={
                              !item.state
                                ? "stat-circle bkYellow"
                                : "stat-circle bkBlue"
                            }
                          >
                            {item.state ? <p>On</p> : <p>Off</p>}
                          </div>
                        </motion.p>
                        <div
                          className="action"
                          onClick={() => {
                            setShowOrderAction(index);
                          }}
                        >
                          <img src={actions} alt="" />
                        </div>
                      </div>

                      <motion.div
                        className="order-list-item order-actions-container"
                        initial={{ height: "0", opacity: 0 }}
                        animate={
                          showOrderAction == index
                            ? {
                                height: "60px",
                                opacity: 1,
                                transition: { delay: 0 },
                              }
                            : {
                                height: "0",
                                opacity: 0,
                                transition: { delay: 0 },
                              }
                        }
                      >
                        <motion.div
                          className="order-action bkRed"
                          initial={{ opacity: 0 }}
                          animate={
                            showOrderAction == index
                              ? { opacity: 1 }
                              : { opacity: 0 }
                          }
                          onClick={() => {
                            setSelectedClientDetail(item);
                            setShowClinetDetails(true);
                          }}
                        >
                          <p>More details</p>
                        </motion.div>
                      </motion.div>
                    </div>
                  );
                })}
          </div>
        </div>

        <div className="clients-right">
          <p>Clients Stat</p>
          <div className="client-stat-item">
            <div className="client-stat-item-left">
              <div className="client-stat-item-img">
                <img src={client} alt="" />
              </div>
              <p>Client Count</p>
            </div>

            <p>
              {" "}
              {allClient.length} <span>(C)</span>
            </p>
          </div>

          <div className="client-stat-item">
            <div className="client-stat-item-left">
              <div className="client-stat-item-img">
                <img src={client} alt="" />
              </div>
              <p>Male Client</p>
            </div>

            <p>
              {maleCount} <span>(C)</span>
            </p>
          </div>

          <div className="client-stat-item">
            <div className="client-stat-item-left">
              <div className="client-stat-item-img">
                <img src={client} alt="" />
              </div>
              <p>Female Clients</p>
            </div>

            <p>
              {femaleCount} <span>(C)</span>
            </p>
          </div>

          <div className="client-stat-item">
            <div className="client-stat-item-left">
              <div className="client-stat-item-img">
                <img src={client} alt="" />
              </div>
              <p>Ratio Order / Client</p>
            </div>

            <p>
              {(ordersCount / allClient.length).toFixed(2)} <span></span>
            </p>
          </div>

          <div className="client-stat-item">
            <img src={client} alt="" />
            <div className="circler-stat">
              <p>{allClient.length} (C)</p>
            </div>
            <p>Clients Count</p>

            <div className="colored-back"></div>
          </div>
        </div>
      </div>
    </motion.div>
  );
}

export default ClientPage;
