import "../styles/TransporterForm.css";

import { motion } from "framer-motion";

import React, { useState, useEffect } from "react";

import exit from "../asstes/dashboard/exit.png";
import loadingIcon from "../asstes/loadingIcon.gif";

import { getDatabase, ref, set, push, get, update } from "firebase/database";

import { auth } from "../Components/database_config/firebaseConfig"; // Import auth
import { updatePassword  } from "firebase/auth"; // Import Firebase Auth methods

import { useNavigate } from "react-router-dom";

function PasswordUpdateLast({ close, setShowPasswordUpdateLast , logOut }) {
  const [password, setName] = useState("");
  const [confirmPassword, setPrice] = useState("");

  const [loading, setLoading] = useState(false);
  const [showMsg, setShowMsg] = useState(false);

  const [successMsg, setSuccessMsg] = useState(false);

  const navigate = useNavigate();

  const clear = () => {
    setName("");
    setPrice("");
  };
  const submit = async () => {
    if (password !== confirmPassword) {
      setShowMsg(true);
      setTimeout(() => setShowMsg(false), 5000); // Hide message after 5 seconds
      return;
    }

    setLoading(true);
    try {
      const user = auth.currentUser; // Get the current logged-in user
      if (user) {
        await updatePassword(user, password); // Update the password
        setSuccessMsg(true); // Show success message
        setTimeout(() => {
          setSuccessMsg(false);
          logOut() // Redirect to home after success
        }, 3000);
      } else {
        throw new Error("No user is logged in. Please log in to update the password.");
      }
    } catch (error) {
      console.log("Error updating password:", error.message);
      setShowMsg(true);
      setTimeout(() => setShowMsg(false), 5000);
    } finally {
      setLoading(false);
    }
  };


  return (
    <motion.div
      className="transporter-Form password-update-form"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1, transition: { delay: 2 } }}
    >
      <div
        className="exit-btn"
        onClick={() => {
          close();
        }}
      >
        <img src={exit} alt="" />
      </div>

      <div className="centre-text">
        <p>Update Admin User Form</p>
        <p className="my-20 grey">
          Change Your Password By Typing The New Password Twice
        </p>
      </div>

      <div className="form-grp flex-column">
        <div className="form-input ">
          <p>Password</p>
          <input
            type="password"
            placeholder="Enter The New Admin Password Here..."
            value={password}
            onChange={(e) => {
              setName(e.target.value);
            }}
          />
        </div>

        <div className="form-input my-20">
          <p>Password Confirmation</p>
          <input
            type="password"
            placeholder="Enter Password Confirmation Here..."
            value={confirmPassword}
            onChange={(e) => {
              setPrice(e.target.value);
            }}
          />
        </div>
      </div>

      <div className="form-actions">
        <div
          className="form-action bkBlue update-btn"
          onClick={() => {
            submit();
          }}
        >
          {" "}
          <motion.p
            className="btn white"
            initial={{ scale: 1, opacity: 1 }}
            animate={
              loading ? { scale: 0, opacity: 0 } : { scale: 1, opacity: 1 }
            }
          >
            Confirmer
          </motion.p>
          <motion.img
            className="loading"
            initial={{ scale: 0, opacity: 0 }}
            animate={
              !loading ? { scale: 0, opacity: 0 } : { scale: 1, opacity: 1 }
            }
            src={loadingIcon}
          />
        </div>
      </div>

      {showMsg && (
        <motion.div className="error-msg" initial={{opacity:0 , scale : 0}} animate={showMsg ? {opacity: 1 , scale:1} : {opacity:0, scale:0}}>
          <p>Passwords Update Failed. Please try again.</p>
        </motion.div>
      )}

      {successMsg && (
       <motion.div className="error-msg" initial={{opacity:0 , scale : 0}} animate={successMsg ? {opacity: 1 , scale:1} : {opacity:0, scale:0}}>
         <p>Password updated successfully! Redirecting to home...</p>
        </motion.div>
      )}
    </motion.div>
  );
}

export default PasswordUpdateLast;
